import React, {useState, useContext, useEffect} from 'react'
import '../CSS/dashboard.css'
import Beer from './Beer'
import Search from './Searchbeer'
import Header from './Header'
import DisplayDash from './DisplayDash'
import axios from 'axios'
import {AuthContext} from './AuthContext'
import setAuthToken from '../setAuthToken'
import Title from './Title'
import SVG from './waves'

export default function Dashboard(props) {

  
if (localStorage.auth_token) {
  setAuthToken(localStorage.auth_token)
}

    const [auth, setAuth] = useContext(AuthContext)
    const [beer, setBeer] = useState([])

    const{isAuthenticated} = auth;

    useEffect(()=> {
       if (!isAuthenticated) {
          props.history.push('./login')
       }// eslint-disable-next-line
    }, [isAuthenticated])
    

    const searchBeer = text => {
      const getBeer = async () => {
          const response = await fetch(`https://api.untappd.com/v4/search/beer?q=${text}&client_id=41EF786235D5A6E859C26C7DABA2048BB19344D0&client_secret=2C5E752380284C4A141AD1066C8E688BF0A299F9`);
          const data = await response.json();
          setBeer(data.response.beers.items)};
        getBeer();
    };

    const beerInfo = (id, tap) => {
      const getBeerInfo = async () => {
          const response = await fetch(`https://api.untappd.com/v4/beer/info/${id}?client_id=41EF786235D5A6E859C26C7DABA2048BB19344D0&client_secret=2C5E752380284C4A141AD1066C8E688BF0A299F9`);
          const data = await response.json();
          const beerObject = data.response.beer;
        beerObject.tap = Number(tap);
          const post = async (beerObject) => {
            const config = {
              headers: {
                'Content-Type': 'application/json',
                'auth_token': localStorage.getItem('auth_token')
              }};

              try{
                const res = await axios.post('/dashboard', beerObject, config);
              } catch (err){
                console.log(err)
              }
          }
          post(beerObject)
        }
         getBeerInfo();
       }

       const addToTap = (bid, tap) =>
{ if(Number(tap) !== 0 && Number(tap) <= 22 ) {
  const display = beer.filter(x => x.beer.bid !== bid);
  setBeer(display);
  beerInfo(bid, tap);}
  else {
       console.log('Please enter a valid tap number')
  }
}

  

    return (
      <div className="dashboard1" style={{zIndex: '-10'}}>
      <Title />
      <div className="header">
      <div className="inner-header">
      <div className="beerTap" style={{display:'block'}}></div>
      <DisplayDash />
      </div>
      <div>
      <SVG />
      </div>
      </div>
      <div className="dashboard-container">
      <Header /> 
          <Search searchBeer={searchBeer}/>
            <div className='Anthony'>
            {beer.map((x, index) => <Beer 
            name={x.beer.beer_name} 
            key={index}
            id={x.beer.bid} 
            img={x.beer.beer_label} 
            style={x.beer.beer_style} 
            abv={x.beer.beer_abv} 
            company={x.brewery.brewery_name} 
            ibu={x.beer.beer_ibu}
            description={x.beer.beer_description}
            addToTap={addToTap}
            />)}
            </div>
          </div>
          </div>
    )
   
}

