import React, { Fragment, useState } from 'react'
import '../CSS/MainDisplay.css'
export default function MainDisplayBeers({name, logo, abv, ibu, beerstyle, brewery, ratingCount, ratingScore, description, tap}) {
    
    const [readMore, setReadMore] = useState(true)

     const btn_action = (x) => {
         if (x)
         { return setReadMore(false)}
         else { return setReadMore(true)}
     }

   
    return (
             <Fragment>
                
            <div className="main-section">
            <div className="tap"><p>TAP</p><p>{tap}</p></div>
            <div className="logo-container">
            <img src= {logo} alt="logo"  style={{width: '100%'}}/>
            </div>
           
            <div className="main-details">
            <h1>{name}</h1>
        <h3>{brewery}</h3>
        <p>{beerstyle}</p>
        </div>
       
        <div className="main-bottom" style={{fontWeight: 800}}>
            <p>{abv}<br/> ABV</p>
            <p>{ibu} <br/> IBU</p>
            <p>{ratingCount} <br/>Rating Count</p>
            <p>{ratingScore.toPrecision(2)}/4 <br/>Rating Score</p>
      </div>
      <div className="main-description" >
          <p className={readMore? "main-description-small": "main-description"}>{description}</p>
      </div>
        {description? <button className="btn btn-readmore" onClick={() => {btn_action(readMore)}}>{readMore? 'Read More':'Read Less'}</button>:''}
      </div>
        </Fragment>
    
    )
}
