import React, { createContext, useState } from 'react'

export const AuthContext = createContext();



export const AuthState = props => {
    const [auth, setAuth] = useState({
        auth_token: '',
        isAuthenticated: null,
    })
    return (
        <AuthContext.Provider
            value={[auth, setAuth]}>
                {props.children}
        </AuthContext.Provider>
    )


};