import React, {Fragment} from 'react'
import '../CSS/displayDashBeer.css'
export default function DisplayDashBeer({name, logo, abv, ibu, beerstyle, brewery, ratingCount, ratingScore, tap,  removeBeer, id}) {
  

    return (
        <Fragment>
        <div className="beer-section beer-section1">
        <div className="dashtap"><p>{tap}</p></div>
            <div className="logo-cont1" >
         <img src= {logo} alt="logo" />
         </div>
            <div className="details details1">
            <h1>{name}</h1>
        <h3>{brewery}</h3>
        <p style={{color: 'white'}}>{beerstyle}</p>
        </div>
       
        <div className="bottom bottom1" style={{marginTop: '2%', marginLeft: '2%'}}>
            <p>{abv}<br/> ABV</p>
            <p>{ibu} <br/> IBU</p>
            <p>{ratingCount} <br/>Rating Count</p>
            <p>{ratingScore.toPrecision(2)}/4 <br/>Rating Score</p>
         

      </div>
      <button type="button" className="remove-btn btn btn-success" onClick={()=> {removeBeer(id)}} style={{ backgroundColor:'red', marginTop: '1%'}}>Remove</button>
      </div>

  </Fragment>
    )

}
