import React, {useContext} from 'react'
import {BeerContext} from '../BeerContext'
import DisplayDashBeer from './DisplayDashBeer'
import axios from 'axios'
import setAuthToken from '../setAuthToken'

export default function DisplayDash() {

  if (localStorage.auth_token) {
    setAuthToken(localStorage.auth_token)
  }

    const beerContext = useContext(BeerContext);
    const {displayBeer, setDisplayBeer} = beerContext

    const removeBeer = id => {
        const deleteBeer = async () => {
         try{
                const res = await axios.delete('/dashboard', {data: {userid: id}});
              setDisplayBeer(res.data)
              } catch (err){
                console.log(err)
              }
        }
        deleteBeer();
       
      };

      const style = {
        display: 'grid',
        gridTemplateColumns: 'repeat(auto-fill, minmax(225px, 1fr))',
        gridTemplateRows: 'repeat(auto-fill, minmax(200px, 1fr))',
         padding: '3%',
         zIndex: '-9999'
      }

    return (
        <div className="displayDash" style={style}>
            {displayBeer.sort((a, b) => a.tap - b.tap).map((x, index) => <DisplayDashBeer 
            name={x.beerName} 
            logo={x.beerLogo} 
            abv={x.beerABV}
            ibu={x.beerIBU}
            beerstyle={x.beerStyle}
            brewery={x.brewery}
            ratingCount={x.ratingCount}
            ratingScore={x.ratingScore}
            key={index} removeBeer={removeBeer}
            id={x._id}
            tap={x.tap}/>)}
        </div>
    )
}
